<template>
    <div class="where">
      <div class="dropdown">
        <button class="dropbtn">FOLLOWER</button>
        <div class="dropdown-content">
          <div style="padding : 10px">
            <h4>ÌåîÎ°úÏõå</h4>
            <input placeholder="Í≤ÄÏÉâ" @input="search($event.target.value)" />
            <div class="post-header" v-for="(follow,id) in followers" :key="id" >
              <div class="profile" :style="`background-image : url(${follow.image})`"></div>
              <span class="profile-name">{{ follow.name }}</span>
            </div>
          </div>
        </div>
      </div>

    </div>

</template>

<script>

import {onMounted, ref} from "vue";
import axios from "axios";

export default {
  name: "FollowerBar",
  setup(){
    let followers = ref([]);
    let followersOriginal = ref([]);

    onMounted(()=>{ //ÎßåÏïΩ publicÌè¥ÎçîÏóê ÎÑ£ÏóàÎã§Î©¥ /Í≤ΩÎ°ú ÌïòÎÇòÎßå Ï§òÎèÑ Ï†ëÍ∑ºÏù¥ Í∞ÄÎä•ÌïòÎã§.
      axios.get('/follower.json').then((e)=>{
        followers.value=e.data
        followersOriginal.value = [...e.data]; //Ïã§ÏßàÏ†ÅÏù∏ ÌéòÏù¥ÏßÄ Î†åÎçîÏãúÏùò Ïò§Î¶¨ÏßÄÎÑêÎ≥∏ Î≥µÏÇ¨
      })
    });

    function search(value){
      let newFollowers = followersOriginal.value.filter((e)=>{
        return e.name.indexOf(value) !== -1;
      });
      followers.value = [...newFollowers];
    }

    return { followers , search };
  },
}
</script>

<style scoped>
.profile {
  width: 30px;
  height: 30px;
  background-size: 100%;
  border-radius: 50%;
  float: left;
  position: relative;
}
.profile-name {
  display: block;
  float: left;
  padding-left: 10px;
  padding-top: 7px;
  font-size: 14px;
}
.post-header {
  height: 30px;
  padding: 10px;
}
.where{
  margin-top: -3%;
  margin-left: 75%;
}
.dropbtn {
  margin-top: 13%;
  background: #888888;
  color: white;
  padding: 5px;
  font-size: 16px;
  border: cornflowerblue;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  position : absolute;
  display: none;
  background-color: #f1f1f1;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 2;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #ddd;}

.dropdown:hover .dropdown-content {display: block;}

.dropdown:hover .dropbtn {background-color: #3e8e41;}
</style>