import { createStore } from 'vuex'
import postData from "@/assets/datas/postData";

const store = createStore({
    state(){
        return {
            dataName : 'im',
            postData
        }
    },
    mutations : {
        liked(state , id){
            if(state.postData[id].liked===false){
                state.postData[id].liked=true;
                state.postData[id].likes= state.postData[id].likes+1
            }else{
                state.postData[id].liked=false;
                state.postData[id].likes=state.postData[id].likes-1
            }
        },
        storePublish(state,newUpload){
            state.postData.unshift(newUpload);
        },
        postDataAxios(state,data){
            state.postData.push(data);
        }
    }
})

export default store